@import url('https://fonts.googleapis.com/css2?family=Chivo+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,800;1,700&display=swap');

$color-1: #141827; 
$color-2: #62455b; 
$color-3: #736681; 
$color-4: #c1d9d0; 
$color-5: #fffae3;

@mixin flexbox($justify, $aling) {
    display: flex;
    justify-content: $justify;
    align-items: $aling;
}